<template>
  <div>
    <!-- 外壳 -->
    <el-form v-model='addRuleForm' :rules="rules">
      <el-card class='box-card box-card-ex'>
        <div slot='header' class='clearfix' style='text-align: left'>
            <el-form-item label='规则名称' style='width:300px;display:inline-block'>
              <el-input v-model='addRuleForm.passRuleName' style="width:70%;margin-left:20px;">
              </el-input>
             </el-form-item>
             <el-form-item label='车场名称' style='width:300px;display:inline-block;margin-left:30px;' prop='parkingId'> 
              <el-select v-model='addRuleForm.parkingId' style='width:200px;' v-show='parkingIdOptionsShow' clearable @change='getSX(addRuleForm.parkingId)'>
                  <el-option
                  v-for='item in parkingIdOptions'
                  :key='item.id'
                  :label='item.label'
                  :value='item.value'>
                  </el-option>
              </el-select>
           </el-form-item>  
           <div></div>
            <b>时间定义</b>
            <el-select v-model='addRuleForm.sjdy' style='margin-left:20px;' v-show='timeShow' @change='handleAddSJ()'>
              <el-option
              v-for='item in timeDYOptions'
              :key='item.id'
              :label='item.label'
              :value='item.value' 
              >
              </el-option>
            </el-select>
            <el-button type='primary' style='margin-left:30px;' @click=''>添加</el-button>
          
        </div>
        <!-- 内容 -->
        <div class='box-card-ex'>
          <el-table
          :data="sjdyARR"
          stripe
          style="width: 100%">
          <el-table-column
            prop="id"
            label="id"
            width="180">
          </el-table-column>
          <el-table-column
            prop="name"
            label="时间名称"
            width="180">
          </el-table-column>
        </el-table>

        </div>
      </el-card>
      <!-- 第二区域 -->
      <el-card class='box-card box-card-ex'>
          <div slot='header' class='clearfix' style='text-align:left'>
              <b>设施定义</b>  
            </div>
          <!-- 内容 -->
          <div class='box-card-ex'>
                 <!-- 路口 -->
              <div v-for='(item,index) in addRuleForm.sjForm' style='border:2px solid #c8c7cc;margin-bottom:15px;padding:10px 15px;border-radius: 10px;'>
                  <div style='text-align: left;margin-bottom:20px;'>
                      <!-- <el-form-item label='车场名称' style='width:300px;' prop='parkingId'> 
                            <el-select v-model='addRuleForm.parkingId' style='width:200px;' v-show='parkingIdOptionsShow' clearable @change='getSX(addRuleForm.parkingId)'>
                                <el-option
                                v-for='item in parkingIdOptions'
                                :key='item.id'
                                :label='item.label'
                                :value='item.value'>
                                </el-option>
                            </el-select>
                         </el-form-item> -->
                    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll"  @change="handleCheckAllChange"
                    style='zoom:120%'>
                      全选</el-checkbox>
                      <div style='margin:15px 0'></div>
                      <el-checkbox-group v-model='checkedItems' @change='handleCheckedItems'>
                        <el-checkbox v-for='item in weekOptions' :label='item' :key='item' style='zoom:120%'>
                          {{item}}
                        </el-checkbox>
                      </el-checkbox-group>
                   </div>
                </div>
          </div>
      </el-card>
      <!-- 第三模块:规则定义 -->
      <el-card class='box-card box-card-ex'>
          <div slot='header' class='clearfix' style='text-align: left'>
              <b>规则定义</b>
          </div>
          <!-- 内容 -->
          <div class='box-card-ex'>
            <div style="">
              <div slot='header' class='clearfix' style='text-align: left'>
                  <b>车辆类型</b>
              </div>
              <div class='box-card-ex'>
                <el-row>
                  <el-col :span='6'>
                    <el-form-item label='车牌颜色' class='laeblStyle'>
                      <el-select
                      v-model='addRuleForm.carColor' class='handle-input' v-show='plateColorOptionsShow'>
                        <el-option
                        v-for='item in carColorOption'
                        :key="item.id"
                        :value='item.value'
                        :label='item.label'>
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span='6'>
                    <el-form-item label='车型' class='laeblStyle'>
                      <el-select
                      v-model='addRuleForm.carType' class='handle-input'>
                        <el-option
                        v-for='item in carTypeOption'
                        :key="item.id"
                        :value='item.value'
                        :label='item.label'>
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span='6'>
                    <el-form-item label='核载人数' class='laeblStyle'>
                      <el-select
                      v-model='addRuleForm.hzrs' class='handle-input'>
                        <el-option
                        v-for='item in hzrsOptions'
                        :key="item.id"
                        :value='item.value'
                        :label='item.label'>
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span='6'>
                    <el-form-item label='自定义分类'  
                    style='position:relative;width:235px;text-align: right' 
                    prop='zdy'>
                      <el-select
                      v-model='addRuleForm.zdy' 
                      class='handle-input' v-show='clflShow' clearable>
                        <el-option
                        v-for='item in clflOption'
                        :key="item.id"
                        :value='item.value'
                        :label='item.label'>
                        </el-option>
                      </el-select>
                      <!-- <div style='position:absolute;right:-75px;top:0'>
                        <el-button type='primary'>添加</el-button>
                      </div> -->
                    </el-form-item>
                  </el-col>
                </el-row>
                <!-- 车辆特征 -->
                <el-row>
                  <!-- <el-col :span='2'>
                    <div style='margin-left:-35px;line-height:40px;'>车牌特征</div>
                  </el-col> -->
                  <el-col :span='6'>
                    <el-form-item label='尾号类型' class='laeblStyle'>
                      <el-select v-model='addRuleForm.tailNumType' class='handle-input'>
                        <el-option
                        v-for='item in sflxOption'
                        :key='item.id'
                        :value='item.id'
                        :label='item.label'>
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span='8'>
                    <el-form-item label='尾号' >
                      <el-input v-model='addRuleForm.tailNum' style='width:70%'>
                      </el-input>
                    </el-form-item>
                  </el-col>
                </el-row>

              </div>
            </div>
              <!-- 通行定义 -->
              <div slot='header' class='clearfix' style='text-align: left'>
                  <b>通行定义</b>
              </div>
              <div class='box-card-ex'>
                <el-row>
                  <el-col :span='6'>
                    <el-form-item label='许可类型' class='laeblStyle'>
                        <el-select
                        v-model='addRuleForm.XK' class='handle-input'>
                          <el-option
                          v-for='item in XKOption'
                          :key="item.id"
                          :value='item.value'
                          :label='item.label'>
                          </el-option>
                        </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span='6'>
                    <el-form-item label='通行类型' class='laeblStyle'>
                        <el-select
                        v-model='addRuleForm.txlx' class='handle-input'>
                          <el-option
                          v-for='item in txlxOption'
                          :key="item.id"
                          :value='item.value'
                          :label='item.label'>
                          </el-option>
                        </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span='6'>
                    <el-form-item label='收费类型' class='laeblStyle'>
                        <el-select
                        v-model='addRuleForm.sflx' class='handle-input'>
                          <el-option
                          v-for='item in sflxOption'
                          :key="item.id"
                          :value='item.value'
                          :label='item.label'>
                          </el-option>
                        </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span='6'>
                    <el-form-item label='费率表' style='width:250px;text-align: right'>
                        <el-select
                        v-model='addRuleForm.flb' style='width:170px;' v-show='xklxOptionShow'>
                          <el-option
                          v-for='item in xklxOption'
                          :key="item.id"
                          :value='item.value'
                          :label='item.label'>
                          </el-option>
                        </el-select>
                    </el-form-item>
                  </el-col>
                </el-row>
              </div>
            </div>
            <div style='float:right;margin:10px 8% 0 0;'>
                <el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click=''>取消</el-button>
              <el-button class="el-button el-button--primary" style='width:80px' @click='addGZ(addRuleForm)'>保存</el-button>
            </div>
            <div style="clear:both"></div>
        </el-card>
    </el-form>
    <!-- 新增时间定义模态框 -->
    <el-dialog title='新增页面' :visible.sync="dialogAddSJ" width="60%">
      <el-form v-model='addSJForm' > 
        <!-- 新增类型。 -->
        <el-card class='box-card box-card-ex'>
        <div slot='header' class='clearfix' style='text-align: left;'>
            时间类型
            <el-select v-model='addSJForm.sjType' class='handle-input' @change='changeSJ(addSJForm.sjType)' style='margin-left:20px;'>
                <el-option
                v-for='item in sjTypeOption'
                :key='item.id'
                :value='item.value'
                :label='item.label'>
                </el-option>
              </el-select>
            <el-button type='primary' style='margin-left:30px;' @click='addSJTypeMsg(addSJForm.sjType)'>添加2</el-button>
        </div>
        <!-- 日期 -->
        <div v-for='(item,index) in addSJForm.rq' style='margin:0 0 10px 0'>
          <el-row style='position: relative'>
            <el-col :span='2'>
                <span style='display:inline-block;font-size:16px;line-height:40px;font-weight:bolder'>日期:</span>
            </el-col>
            <el-col :span='8'>
                <el-date-picker
                v-model="item.rqsj"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期" style='width:270px;'>
              </el-date-picker>
              </el-col>
              <!-- 时间 -->
              <el-col :span='3'>
                  <span style='display:inline-block;font-size:16px;line-height:40px;font-weight:bolder'>时间:</span>
              </el-col>
            <el-col :span='8'>
                <el-time-picker
                    is-range
                    v-model="item.sj"
                    range-separator="至"
                    start-placeholder="开始时间"
                    end-placeholder="结束时间"
                    placeholder="选择时间范围" style='width:270px;'>
                  </el-time-picker>
                  <el-button type='default' icon='el-icon-minus' style='position: absolute;right:0px' @click='remove(index,item)'></el-button>
            </el-col>
          </el-row>
        </div>
        <!-- 星期 -->
        <div v-for='(item2,index) in addSJForm.xq' style='margin:0 0 10px 0'>
          <el-row style='position: relative'>
            <el-col :span='2'>
                <span style='display:inline-block;font-size:16px;line-height:40px;font-weight:bolder'>星期:</span>
            </el-col>
            <el-col :span='8' >
              <div style='margin-top:5px;text-align: left;'>
                <el-checkbox v-model='item2.checked1Val' style='zoom:110%'>周一</el-checkbox>
                <el-checkbox v-model='item2.checked2Val' style='zoom:110%'>周二</el-checkbox>
                <el-checkbox v-model='item2.checked3Val' style='zoom:110%'>周三</el-checkbox>
                <el-checkbox v-model='item2.checked4Val' style='zoom:110%'>周四</el-checkbox>
                <el-checkbox v-model='item2.checked5Val' style='zoom:110%'>周五</el-checkbox>
                <el-checkbox v-model='item2.checked6Val' style='zoom:110%'>周六</el-checkbox>
                <el-checkbox v-model='item2.checked7Val' style='zoom:110%'>周日</el-checkbox>
              </div>
          </el-col>
            <el-col :span='3'>
                <span style='display:inline-block;font-size:16px;line-height:40px;font-weight:bolder'>时间:</span>
            </el-col>
            <el-col :span='8'>
                <el-time-picker
                    is-range
                    v-model="item2.sjfw"
                    range-separator="至"
                    start-placeholder="开始时间"
                    end-placeholder="结束时间"
                    placeholder="选择时间范围" style='width:270px;'>
                  </el-time-picker>
                <el-button type='primary' icon='el-icon-minus' style='position: absolute;right:0px' @click='removeXQ(index,item2)'></el-button>
            </el-col>
          </el-row>
        </div>
        <!-- 假日 -->
        <div style='margin:0 0 10px 0' v-for='item in addSJForm.jjr'>
          <el-row >
            <el-col :span='2'>
              <span style='display:inline-block;font-size:16px;line-height:40px;font-weight:bolder'>节假日:</span>
          </el-col>
          <el-col :span='9'>
            <div style='margin-top:12px;text-align: left'>
              <el-checkbox v-model='item.jjrVal' style='zoom:110%'>允许</el-checkbox>
              <el-checkbox v-model='item.jjrVal2' style='zoom:110%'>不包括</el-checkbox>
            </div>
          </el-col>
          <el-col :span='3'>
              <span style='display:inline-block;font-size:16px;line-height:40px;font-weight:bolder'>时间:</span>
          </el-col>
          <el-col :span='9'>
              <el-time-picker
                  is-range
                  v-model="item.sjfw"
                  range-separator="至"
                  start-placeholder="开始时间"
                  end-placeholder="结束时间"
                  placeholder="选择时间范围" style='width:300px;'>
                </el-time-picker>
          </el-col>
          </el-row>

        </div>

                 <!-- 按钮行 -->
          <div style='float:right;margin:20px 5% 0 0;'>
            <el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click='cancel()'>取消</el-button>
          <el-button class="el-button el-button--primary" style='width:80px' @click='saveEditMsg(addSJForm)'>保存</el-button>
        </div>
        <div style="clear:both"></div>
      </el-card>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
  import moment from 'moment'
  export default{
    data() {
      //验证表单输入项
      var checkParkingId=(rule,value,callbak)=>{
        console.log(callbak);
        if(!this.addRuleForm.parkingId){
          callbak(new Error('请选择停车场'))
        }else{
          callbak();
        }
      }
      var checkCarType=(rule,value,callbak)=>{
        if(!this.addRuleForm.zdy){
          callbak(new Error('请选择车分类'))
        }else{
          callbak();
        }
      }
      return {
        rules:{
          parkingId:[{required:true,validator:checkParkingId,tigger:"blur"}],
          zdy:[{required:true,validator:checkCarType,tigger:"blur",message:"请选择车分类"}],
        },
        //尾号类型
        sflxOption:[
          {id:1,value:1,label:"单号"},
          {id:2,value:2,label:"双号"}
        ],
        txlxOption:[],
        hzrsOptions:[],
        XKOption:[
          {id:0,value:0,label:"禁止"},
          {id:1,value:1,label:"允许"},
        ],
        clflOption:[],
        clflShow:false,
        xklxOptionShow:false,
        plateColorOptionsShow:false,
        parkingIdOptions:[],
        parkingIdOptionsShow:false,
        sjdyARR:[
        ],
        timeShow:false,
        timeDYOptions:[],
        weekArray:[],
        // 星期数组
        weekOptionVal:[
        "周一","周二","周三","周四","周五","周六","周日",
        ],
        xklxOption:[],
        carTypeOption:[
          // {id:1,value:1,label:"小型车"},
          // {id:2,value:2,label:"大型车"},
        ],
        carColorOption:[ ],
        // weekOptions:[
        //     "入口一","出口一","入口二"
        //   ],
        weekOptions:[],
        checkedItems:[],
        addRuleForm:{
          sjForm:[
            {startTime:"",endTime:"",}
        ]
        },
        dialogAddSJ:false,
        addSJForm:{
          rq:[],
          xq:[
            // { "checked1Val":"",
            //   "checked2Val":"",
            //   "checked3Val":"",
            //   "checked4Val":"",
            //   "checked5Val":"",
            //   "checked6Val":"",
            //   "checked7Val":"",
            // },
          ],
          jjr:[],
        },
        sjTypeOption:[
          {id:1,value:1,label:"日期"},
          {id:2,value:2,label:"星期"},
          {id:3,value:3,label:"假期"},
        ],
        isIndeterminate:true,
        checkAll:false,


      }
    },
    methods: {
       //保存规则
       addGZ(val){
        var self=this;
        var ssArr=[];
        console.log(self.addRuleForm.hzrs)
        for(var i=0;i<this.checkedItems.length;i++){
          ssArr[i]=this.checkedItems[i]
        }
        ssArr=ssArr.join(",");
        console.log(self.addRuleForm.carType);

        var url=self.baseUrl+"passRule/add";
        var params={
          "parkingId":self.addRuleForm.parkingId,
          "passRuleName":self.addRuleForm.passRuleName,
          "timeId":self.sjdyARR[0].id,  //待修改
          "facility":self.checkedItems.join(','),
          "vehicleClassifyId":self.addRuleForm.zdy,
          "plateColorNo":self.addRuleForm.carColor,
          "vehicleTypeId":self.addRuleForm.carType,
          "allowType":self.addRuleForm.XK,
          "passTypeId":self.addRuleForm.txlx,
          "feeTypeId":self.addRuleForm.sflx,
          "feeRuleId":self.addRuleForm.flb,
          "vehicleHzrs":self.addRuleForm.hzrs,
          // 车牌特征。
          'plateFeature':{
            "tailNumType":self.addRuleForm.tailNumType,
            "tailNum":self.addRuleForm.tailNum
          }
        }
        self.$axios.post(url,params).then(function(res){
          console.log("新增车牌")
          console.log(res.data)
          self.$message({
            type:'info',
            message:res.data.message
          })
        }).catch(err=>{console.log(err)})
      },

      //收费类型
      sflx(){
         var self=this;
         var url=self.baseUrl+"passRule/initFeeType";
        self.$axios.get(url).then(function(res){
          for(var i=0;i<res.data.data.length;i++){
            self.sflxOption[i]={
              id:res.data.data[i].feeTypeId,
              value:res.data.data[i].feeTypeId,
              label:res.data.data[i].feeType,
            }
          }
        })

      },
      //通行类型
      txlx(){
        var self=this;
        var url=self.baseUrl+"passRule/initPassType";
        self.$axios.get(url).then(function(res){
          for(var i=0;i<res.data.data.length;i++){
            self.txlxOption[i]={
              id:res.data.data[i].passTypeId,
              value:res.data.data[i].passTypeId,
              label:res.data.data[i].passType,
            }
          }
        })
      },
      //核载人数
      hzrs(){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/initJszHzrs";
        self.$axios.get(url).then(function(res){
          console.log(res.data.data)
          for(var i=0;i<res.data.data.length;i++){
            self.hzrsOptions[i]={
              id:res.data.data[i].id,
              value:res.data.data[i].id,
              label:res.data.data[i].hzrs,
            }
          }
        }).catch(err=>{console.log(err)})
      },
      //驾驶证cclx
      jszCllx(){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/initJszCllx";
        self.$axios.get(url).then(function(res){
          console.log("驾驶证")
          console.log(res.data.data)
          for(var i=0;i<res.data.data.length;i++){
            self.carTypeOption[i]={
              id:res.data.data[i].id,
              value:res.data.data[i].id,
              label:res.data.data[i].vehicleTypeName,
            }
          }
        }).catch(err=>console.log(err))
      },
      //车辆分类
      getCarType(){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/initVehicleClassify";
        self.$axios.get(url).then(function(res){
          console.log('车辆分类')
          console.log(res.data)
          self.clflShow=true;
          for(var i=0;i<res.data.data.length;i++){
            self.clflOption[i]={
              id:res.data.data[i].id,
              value:res.data.data[i].id,
              label:res.data.data[i].vehicleClassifyName,
              
            }
          }
        })
      },

      //费率表
      getFL(val){
        console.log('val');
        var self=this;
        var url=self.baseUrl+"passRule/initFeeRule?parkingId=1";
        self.$axios.get(url).then(function(res){
          self.xklxOptionShow=true;
          for(var i=0;i<res.data.data.length;i++){
            self.xklxOption[i]={
              key:res.data.data[i].id,
              value:res.data.data[i].id,
              label:res.data.data[i].feeRuleName,
            }
          }
        }).catch(err=>{console.log(err)});
      },
      // 车牌颜色
		getCarColor(){
			var self=this;
			var url=self.baseUrl+"vehicleInfo/plateColorList"
			self.$axios.get(url).then(function(res){
				self.plateColorOptionsShow=true;
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.carColorOption[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].plateColor,
							value:res.data.data[i].plateColorNo,
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
		},
      getSX(val){
        var self=this;
        self.weekOptions=[];
        var url=self.baseUrl+"passRule/initEnterExit?parkingId="+val;
        self.$axios.get(url).then(function(res){
          for(var i=0;i<res.data.data.length;i++){
            self.weekOptions[i]=res.data.data[i].enterExitName
          }
          self.$forceUpdate();
        }).catch(err=>console.log(err));
        console.log(self.weekOptions)
        this.getFL(val);
      },
      getParkingId(){
        var self=this;  
        var url=self.baseUrl+"vehicleInfo/initParkingList?userId="+localStorage.getItem("userId");
			self.$axios.get(url).then(function(res){
        self.parkingIdOptionsShow=true;
        if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.parkingIdOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].parkingName,
							value:res.data.data[i].id
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
      },
      remove(val,item){
        var i=this.addSJForm.rq.indexOf(item);
        console.log(i)
        this.addSJForm.rq.splice(val,1);
      },
      removeXQ(index,item){
        var i=this.addSJForm.xq.indexOf(item);
        console.log(i)
        console.log(this.addSJForm.xq)
        this.addSJForm.xq.splice(index,1)
      },
      saveEditMsg(val){
        console.log("val")
        console.log(val)
      },
      cancel(){
        this.dialogAddSJ=false;
      },
      handleCheckedItems(val){
        console.log("val")
        console.log(val)
        let checkedCount = val.length;
        this.checkAll=checkedCount ==this.weekOptions.length;
        this.isIndeterminate=checkedCount>0&&checkedCount<this.weekOptions.length
        
      },
      handleCheckAllChange(val){
        console.log("myVal"+val)
        this.checkedItems=val?this.weekOptions:[]
        this.isIndeterminate=false;
      },
      addSJTypeMsg(val){
        console.log("val")
        console.log(val)
        var self=this;
        if(val=='1'){
          self.addSJForm.rq.push(
            {
              id:1
            }
          )
        }else if(val=='2'){
          self.addSJForm.xq.push({
            id:2
          })
        }else{
          self.addSJForm.jjr.push({
            name:"节假日"
          })
        }
      },
      handleAddSJ(){
        var self=this;
        var ARR=[];
        for(var i=0;i<self.timeDYOptions.length;i++){
          //判断 相同跳出
          if(self.timeDYOptions[i].value==self.addRuleForm.sjdy){
            self.sjdyARR.push({
              id:self.addRuleForm.sjdy,
              name:self.timeDYOptions[i].label
            })
          }
        }
        console.log('self.sjdyARR')
        console.log(self.sjdyARR)
        self.$forceUpdate();
      },
      changeSJ(val){
        console.log(val)
      },
      getTimeOptions(){
        var self=this;
        var url=self.baseUrl+"passRuleTime/list";
        self.$axios.get(url).then(function(res){
          self.timeShow=true;
          for(var i=0;i<res.data.data.length;i++){
            self.timeDYOptions[i]={
              value:res.data.data[i].id,
              label:res.data.data[i].timeName,
              key:res.data.data[i].id
            }
          }
        }).catch(err=>{console.log(err)})
      }
    },
    // 钩子函数
    mounted:function(){
      var self=this;
      this.getTimeOptions();
      this.getParkingId();
      this.getCarColor();
      this.getCarType();
      this.jszCllx();
      this.hzrs();
      this.txlx();
      this.sflx();
      self.$forceUpdate();
    }
  }
</script>

<style scoped>
.el-checkbox{
  margin-left:8px !important;
}
.clearfix:before,
.clearfix:after{
  display:table;
  content:"";
}
.clearfix:after{
  clear:both;
}
.box-card-ex{
  margin-top:15px !important;
}
.handle-input{
  width:140px;
  display: inline-block;
}
.laeblStyle{
  width:220px;
  text-align:right
}
.timeStyle{
  width:300px;
  text-align:right;
}
.handle-date{
  width:150px;
}
</style>



